import React, { useState } from 'react';
import './App.css';
import { Window, TitleBar, Button } from "react-desktop/macOs";
import DialogInfo1 from "./Components/DialogInfo1"
import Pin1 from './Components/Pin1';
import ActualApp from './Components/ActualApp';
import { Container } from 'react-bootstrap';

function App() {
  const [submitted, changeSubmitted] = useState(false);
  const [firstPin, changeFirstPin] = useState(null);
  return (
    <Window
      chrome
      height="100vh"
      padding="10px"
      className=""
    >
      <TitleBar title="Fur pat" controls />
      <Container fluid className="d-flex justify-content-center align-items-center">
        {firstPin === "111111" ? <ActualApp /> : submitted ? <div><Pin1 changeFirstPin={changeFirstPin} /></div> : <div><DialogInfo1 changeSubmitted={changeSubmitted} /></div>}
        {/* <Button onCLick={()=>localStorage.removeItem('localRandka')}>Clear</Button> */}
      </Container>
    </Window>
  );
}

export default App;
