import React, { Component } from 'react';
import { SegmentedControl, SegmentedControlItem, Text, Box, Button } from 'react-desktop/macOs';
import DateForm1 from './DateForm1';
import DateForm2 from './DateForm2';

export default class ActualApp extends Component {
    constructor() {
        super();
        this.state = { selected: 1 }
    }

    render() {
        return (
            <SegmentedControl box>
                {this.renderItems()}
            </SegmentedControl>
        );
    }

    renderItems() {
        return [
            this.renderItem(1, 'Information',
                <Box>
                    <Text>To Proceed even further please take part in this short survey at 2nd Tab so called "Survey"; Or click button below to be redirected.</Text>
                    <Button color="blue" onClick={() => this.setState({ selected: 2 })}>Take me to the survey</Button>
                </Box>),
            this.renderItem(2, 'Survey',
                <DateForm1/>
            ),
            this.renderItem(3, 'Last Step',
                <DateForm2/>

            )
        ];
    }

    renderItem(key, title, content) {
        return (
            <SegmentedControlItem
                key={key}
                title={title}
                selected={this.state.selected === key}
                onSelect={() => this.setState({ selected: key })}
            >
                {content}
            </SegmentedControlItem>
        );
    }
}

