import React, { useState } from 'react'
import { Dialog, Button } from 'react-desktop/macOs'
export default function DialogInfo({ changeSubmitted }) {
    const [agree, changeAgree] = useState(false);
    return (
        <>
            {!agree ? <Dialog1 changeAgree={changeAgree}/> : <Dialog2 changeSubmitted={changeSubmitted}/>}
        </>
    )
}



export function Dialog1({changeAgree}) {
    return (
        <Dialog
            title="Important Information"
            message="Please use Chrome or Chromium based browser to browse this website!"
            icon="❤❤❤❤❤❤❤❤❤❤❤❤"
            buttons={[
                <Button color="blue" onClick={() => changeAgree(true)}>I'm using Chromium based browser</Button>,
            ]}
        />
    )
}
export function Dialog2({changeSubmitted}) {
    return (
        <Dialog
            title="Important Information"
            message="To proceed further please enter date written down in small black diary."
            icon="❤❤❤❤❤❤❤❤❤❤❤❤"
            buttons={[
                <Button color="blue" onClick={() => changeSubmitted(true)}>Submit</Button>,
            ]}
        />
    )
}
