import React from 'react'
import { Pin, Box, Text } from 'react-desktop/macOs'

export default function Pin1({changeFirstPin}) {
    return (
        <>
            <Box>
                <Text className="text-center">
                    Type Date Like: "DD/MM/RR"
                </Text>
            </Box>
            <br />
            <Pin
                onChange={value => changeFirstPin(value)}
                length={6}
                reveal
            />
        </>
    )
}
