import React, { useState } from 'react'
import { Button, Radio, Label, Text, ProgressCircle } from 'react-desktop/macOs';
import { Form } from 'react-bootstrap';
import { firestore } from '../firebase'
import swal from 'sweetalert';


export default function DateForm2() {
    const [loading, setLoading] = useState(false);

    const [ yes, setYes ] = useState("no")



    const handleSubmit = (e) => {
        e.preventDefault();
        setLoading(true);
        firestore.collection("dateFrom2").add({
            yes: yes
        })
        .then(()=>{
            localStorage.setItem("localRandka", true)
            swal("Thanks!", "Thanks for taking part in my quick survey. Please tell me if you are done with all two forms","success")
            setLoading(false);
        })
        .catch(err=>{
            setLoading(false);
            window.alert(err.message);
        })
    }

    return (
        <Form onSubmit={handleSubmit}>
            <Label>Are you interested in idea of date with user: 'Krzyś'</Label>
            <Radio
                label="Yes!"
                name="radio1"
                defaultValue="yes"
                onChange={(e)=>setYes(e.target.value)}
            />
            <Radio
                label="No..."
                name="radio1"
                defaultValue="no"
                onChange={(e)=>setYes(e.target.value)}

            />
            <Button type="submit" color="blue">{!loading ? <Text style={{ color: "#fff" }}>Submit</Text> : <ProgressCircle />}</Button>

        </Form>
    )
}
