import React, {useEffect, useState} from 'react'
import { Container } from 'react-bootstrap'
import DateForm from "./DateForm"




export default function ActualApp() {
    // const Randka = firestore.collection("dateForm2").orderBy("timestamp").limit(1);
    
    const localRandka = localStorage.getItem("localRandka");
    const [Randka, setRandka] = useState(localRandka?localRandka:null) 
    useEffect(()=>{
        setRandka(localRandka)
    },[localRandka])
    
    return (
        <Container fluid>
            {Randka === null ?  <DateForm/> : (Randka === true? <div>True</div> : <div>Not False neither True</div>)}
        </Container>
    )
}
