import React, { useState } from 'react'
import {  Button, TextInput, Label, Checkbox, Text, ProgressCircle } from 'react-desktop/macOs';
import { Form } from 'react-bootstrap';
import { firestore } from '../firebase'

export default function DateForm1() {
    const [loading, setLoading] = useState(false);



    const [name, setName] = useState("");
    const [animals, setAnimals] = useState("");
    const [krzys, setKrzys] = useState("");
    const [dkrzys, setDKrzys] = useState("");
    const [memes, setMemes] = useState("");
    const [roblox, setRoblox] = useState("");
    const [mkrzys, setMKrzys] = useState("");
    const [anime, setAnime] = useState("");
    const [tvshows, setTVShows] = useState("");
    const [play, setPlay] = useState("");
    const [talk, setTalk] = useState("");

    const handleSubmit = (e) => {
        e.preventDefault();
        setLoading(true);
        firestore.collection("dateFrom1").add({
            name: name,
            animals: animals,
            krzys: krzys,
            dkrzys: dkrzys,
            memes: memes,
            roblox: roblox,
            mkrzys: mkrzys,
            anime: anime,
            tvshows: tvshows,
            play: play,
            talk: talk
        })
        .then(()=>{
            setLoading(false);
        })
        .catch(err=>{
            setLoading(false);
            window.alert(err.message);
        })
    }

    return (
        <Form onSubmit={handleSubmit}>
            <TextInput label="Name" onChange={e => setName(e.target.value)} />
            <Label>Please Select all that matches:</Label>
            <Checkbox
                label="I Like cute Animals"
                onChange={(e) => setAnimals(e.target.checked)}
            />
            <Checkbox
                label="I don't like Krzyś"
                onChange={(e) => setKrzys(e.target.checked)}
                defaultValue="false"

            />
            <Checkbox
                label="I Like Krzyś"
                onChange={(e) => setDKrzys(e.target.checked)}
                defaultValue="true"

            />
            <Checkbox
                label="I Like Memes"
                onChange={(e) => setMemes(e.target.checked)}
                defaultValue="true"

            />
            <Checkbox
                label="I Like Roblox"
                onChange={(e) => setRoblox(e.target.checked)}
                defaultValue="true"

            />
            <Checkbox
                label="I kinda missed Krzyś"
                onChange={(e) => setMKrzys(e.target.checked)}

                defaultValue="true"

            />
            <Checkbox
                label="I would like to watch anime with someone"
                onChange={(e) => setAnime(e.target.checked)}

                defaultValue="true"

            />
            <Checkbox
                label="I would like to watch TV shows with someone"
                onChange={(e) => setTVShows(e.target.checked)}
                defaultValue="true"

            />
            <Checkbox
                label="I would like to talk over night with someone"
                onChange={(e) => setPlay(e.target.checked)}
                defaultValue="true"

            />
            <Checkbox
                label="I would like to play Roblox/LOL with someone"
                onChange={(e) => setTalk(e.target.checked)}
                defaultValue="true"

            />
            <Button type="submit" color="blue">{!loading? <Text style={{color: "#fff"}}>Submit</Text>:<ProgressCircle/>}</Button>
        </Form>
    )
}
